.Persik {
  display: block;
  width: 100%;
  max-width: 240px;
  margin: 20px auto;
}



.parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  gap: 12px;

  position: absolute;
  width: 270px;
  height: 130px;
  left: 34px;
  top: 194px;


}

.title{
  width: 193px;
  height: 29px;
  margin: 1em;

  font-family: 'SF Pro Display';
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  /* identical to box height */



  /* Inside auto layout */

  flex: none;
  order: 0;
  flex-grow: 0;
  
}

.svg{
  position: absolute;
  width: 301px;
  height: 174px;
  left: 48px;
  top: 376px;
}



.button{
  position: absolute;
  height: 54px;
  left: 8.97%;
  right: 8.72%;
  top: calc(50% - 54px/2 + 290px);
  

}



.discription{
  width: 270px;
  height: 89px;

  font-family: 'SF Pro Display';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.0025em;


  /* Inside auto layout */

  flex: none;
  order: 1;
  flex-grow: 0;
}
